<template>
  <div class="p-px-4 a-bg-white">
    <h3 class="p-py-2 p-m-0">Mening kalendarim</h3>
    <div class="a-border">
      <div class="p-grid p-nogutter">
        <div class="p-col-6 p-md-5 p-lg-4 p-col-nogutter">
          <div class="a-bg-image"></div>
        </div>
        <div class="p-col-6 p-md-7 p-lg-8 p-d-flex p-ai-center">
          <div>
            <h2 class="p-m-0">Rustam<br>Husanboyev</h2>
            <small class="p-text-secondary">Import/Eksport bo'limi</small>
          </div>
        </div>
      </div>
    </div>
    <div class="p-py-3">
      <FullCalendar :events="events" :options="options" class="a-calendar"/>
    </div>
  </div>
</template>


<script>
import '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import {defineComponent} from "vue";

export default defineComponent({
  data () {
    return {
      options: {
        plugins:[dayGridPlugin, timeGridPlugin, interactionPlugin],
        initialDate : new Date(),
        initialView: 'timeGridDay',
        customButtons: {
          mybutton: {
            text: 'Yangi vazifa',
            click: () => {
              alert("Yangi vazifa qo'shildi")
            }
          }
        },
        titleFormat() {
          return `Payshanba, Sentyabr ${new Date().getDate()}-${new Date().getDate() + 2}`
        },
        allDayText: "Butun kun",
        nowIndicator: true,
        select: function (arg) { // Belgilangan vaqtga event belgilash
          let title = prompt('Enter title:');
          if (title) {
            this.addEvent({
              title: title,
              start: arg.start,
              end: arg.end,
              allDay: arg.allDay
            })
          }
          this.unselect()
        },
        eventClick: function (arg) { // event ustiga bosganda ishga tushadi
          if (confirm('Are you sure you want to delete this event?')) {
            arg.event.remove() // tanlangan eventni o'chiradi
          }
        },
        headerToolbar: {
          left: 'title prev,next',
          // center: 'prev,next',
          right: 'mybutton'
        },
        editable: true,
        selectable:true,
        selectMirror: true,
        dayMaxEvents: true
      },
      events: [ // eventlar ro'yxati
        {
          "id": 1,
          "title": "Tadbir bor",
          "start": `2021-09-25T09:00:00`,
          "end": `2021-09-25T10:30:00`
        },
        {
          "id": 2,
          "title": "Vazifa bor",
          "start": `2021-09-25T06:00:00`,
          "end": `2021-09-25T08:00:00`
        }
      ]
    }
  }
})
</script>


